<template>
    <div id="stock">
        <h1>Ressources en stock</h1>
        <ul class="ressources">
            <li v-for="ressource in ressources" :key="ressource.key"
            :class="['ressource','item', ressource.key]">
                <div :class="['icon', ressource.key]"></div>
                <span class="stock">{{ressource.stock}}</span>
                <span class="name">{{ressource.name}}</span>
                <p class="description">{{ressource.description}}</p>
            </li>
        </ul>
        <div class="consommation">
        <h1>Consommation par jour</h1>
        <p>Les escouades et équipes scientifiques ont besoin de boire et manger. Veillez à garder des réserves suffisantes.</p>
        {{getFoodConsumedPerDay()}} <div class="icon food"></div> par jour &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        {{getWaterConsumedPerDay()}} <div class="icon water"></div> par jour
        </div>
    </div>
</template>

<script>
import { state, getFoodConsumedPerDay, getWaterConsumedPerDay } from "@/game";
import { ressources } from "@/ressources";

export default {
  name: "Stock",

  computed: {
    ressources() {
      return Object.entries(state.ressources).map(([key, stock]) => {
        return { key, stock, ...ressources[key] };
      });
    }
  },

  methods: {
    getFoodConsumedPerDay,
    getWaterConsumedPerDay
  }
};
</script>

